@charset "UTF-8";
:root {
  --font-size-1: 5rem;
  --font-size-2: 4.5rem;
  --font-size-3: 4rem;
  --font-size-4: 3rem;
  --font-size-5: 2.5rem;
  --font-size-6: 20px;
  --space-1: 10rem;
  --space-2: 8rem;
  --space-3: 6rem;
  --space-4: 3rem;
  --t-space-1: 4rem;
  --t-space-2: 3rem;
  --t-space-3: 2.5rem;
  --t-space-4: 2rem;
  --color-1: #e94e4e;
  --color-2: #212121;
  --color-3: #fcecd4;
  --color-4: #5a3e36;
  --color-5: #fff;
  --t-color: #212121;
  --b-color: #242424;
  --link-color: #0a85ff;
  --bg-1: #f7f7f7;
  --st: 0.3s all;
  --title-line-height: 1.2;
  --body-line-height: 1.7;
  --site-max-width: 1440px;
}
@media (min-width: 834.6px) {
  :root {
    --site-width: 90%;
  }
}
@media (max-width: 834.5px) {
  :root {
    --site-width: calc(100% - 50px);
  }
}

