  }
  .header-main .header-main_inner .site-logo img {
    object-fit: contain;
    height: auto;
    width: 100%;
  }
  .main-navigation #primary-menu {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    list-style: none;
    margin-top: 0;
    margin-bottom: 0;
  }
  .main-navigation #primary-menu ul.sub-menu {
    background-color: var(--color-4);
    min-width: 200px;
    filter: drop-shadow(2.5px 2.5px 5px rgba(0, 0, 0, 0.05));
    border-radius: 4px;
    transition: var(--st);
    position: absolute;
    opacity: 0;
    display: block;
    pointer-events: none;
    transform: translateY(30px);
  }
  .main-navigation #primary-menu ul.sub-menu li:not(:last-child) a {
    margin-bottom: 0.5rem;
  }
  .main-navigation #primary-menu ul.sub-menu li {
    padding: 0.5rem 1rem 0.5rem 2rem;
  }
  .main-navigation #primary-menu ul.sub-menu li:first-child {
    padding-top: 1rem;
  }
  .main-navigation #primary-menu ul.sub-menu li:last-child {
    padding-bottom: 1rem;
  }
  .main-navigation #primary-menu ul.sub-menu li a {
    width: 100%;
    display: flex;
  }
  .main-navigation #primary-menu ul ul {
    left: 100%;
    top: 0;
  }
  .main-navigation #primary-menu ul ul ul {
    left: unset;
    right: 100%;
    top: 0;
  }
  .main-navigation #primary-menu > li > .sub-menu {
    top: 100%;
  }
  .main-navigation #primary-menu .menu-item-has-children {
    position: relative;
    display: flex;
    align-items: center;
  }
  .main-navigation #primary-menu .menu-item-has-children a {
    text-decoration: none;
  }
  .main-navigation #primary-menu .menu-item-has-children:hover > ul.sub-menu {
    opacity: 1;
    transform: translateY(0);
    pointer-events: all;
  }
  .main-navigation #primary-menu > li:not(:last-child) {
