  .main-navigation #primary-menu > li a {
    color: var(--t-color);
  }
  .main-navigation #primary-menu > li .sub-menu-trigger {
    float: right;
    display: flex;
    transform: rotate(45deg);
    margin-left: 1rem;
    pointer-events: none;
    cursor: pointer;
    transition: var(--st);
  }
  .main-navigation #primary-menu > li .sub-menu-trigger svg {
    stroke: var(--color-1);
  }
  .main-navigation #primary-menu li li:hover > .sub-menu-trigger {
    transform: rotate(0deg);
  }
  .main-navigation #primary-menu li li li:hover > .sub-menu-trigger {
    transform: rotate(180deg);
  }
  .main-navigation #primary-menu li:hover > .sub-menu-trigger {
    transform: rotate(90deg);
    top: 1px;
    right: 4px;
  }
