label[for=nav-toggle] {
  opacity: 0;
  font-size: 0;
}

@media (max-width: 1199.5px) {
  .nav_btn {
    display: none;
  }
  .header-main {
    position: sticky;
    top: 0;
    background-color: var(--color-4);
    height: 70px;
    width: 100%;
    box-sizing: border-box;
    z-index: 9;
  }
  .header-main .header-main_inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    height: 100%;
  }
  .header-main .site-logo {
    width: 200px;
    max-width: 50%;
    margin: 0;
    display: flex;
    max-height: calc(100% - 15px);
  }
  .header-main img {
    height: 100%;
    object-fit: contain;
    width: 100%;
  }
  .header-main .main-navigation ul {
    display: none;
  }
  .header-main #primary-menu {
    position: absolute;
    width: 100%;
    height: calc(100vh - 69px);
    overflow: auto;
    background-color: var(--color-3);
    left: 0;
    margin-top: 20px;
    padding: 25px;
    z-index: 9;
  }
  .header-main #primary-menu li {
    position: relative;
    display: grid;
    align-items: center;
    grid-template-columns: calc(100% - 55px) 25px;
    column-gap: 30px;
  }
  .header-main #primary-menu li a {
    padding: 1rem 1.5rem;
    color: var(--t-color);
    opacity: 0.75;
    font-size: 20px;
    width: 100%;
    display: inline-flex;
  }
  .header-main #primary-menu li.current-menu-item a {
    opacity: 1;
  }
  .header-main #primary-menu li.menu-item-has-children a {
    grid-column: 1/2;
  }
  .header-main #primary-menu li .sub-menu-trigger {
    width: 25px;
    transform: rotate(90deg);
    transition: var(--st);
    grid-column: 2/3;
    margin-top: 1.5rem;
  }
  .header-main #primary-menu li > .sub-menu {
    width: 100%;
    padding-left: 20px;
    grid-column: 1/3;
  }
  .header-main #primary-menu li > .sub-menu li {
    padding-block: 5px;
  }
  .header-main #primary-menu li > .sub-menu li a {
    font-size: 18px;
  }
  .header-main #primary-menu li > .sub-menu img {
    width: 30px;
    position: absolute;
    top: 0.5rem;
    left: 0;
    object-position: top;
  }
  .header-main #primary-menu li > .sub-menu a {
    padding: 0.5rem 2rem 0 15px;
    font-size: 20px;
  }
  .header-main #primary-menu li.sub-menu-open > .sub-menu-trigger {
    transform: rotate(180deg);
    margin-top: 1rem;
  }
  /* Nav Trigger Start */
  .hamburger {
    height: 30px;
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  .hamburger > div {
    position: relative;
    top: 0;
    left: 0;
    background: #dad5d5;
    height: 2px;
    width: 100%;
    transition: all 0.4s ease;
  }
  .hamburger > div::before, .hamburger > div:after {
    content: "";
    position: absolute;
    top: -10px;
    background: #e2dede;
    width: 100%;
    height: 2px;
    transition: all 0.4s ease;
  }
  .hamburger > div:after {
    top: 10px;
  }
  .menu-toggle {
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 99;
  }
  .menu-toggle:checked + div {
    background: rgba(0, 0, 0, 0);
  }
  .menu-toggle:checked + div:before {
    top: 0;
    transform: rotate(45deg);
    background: rgb(228, 222, 222);
  }
  .menu-toggle:checked + div:after {
    top: 0;
    transform: rotate(135deg);
    background: rgb(240, 234, 234);
  }
  /* Nav Trigger End */
}
