// Buttons

@mixin btn {
  padding: 1rem 2.5rem;
  width: fit-content;
  min-width: 170px;
  display: inline-flex;
  box-sizing: border-box;
  justify-content: center;
  transition: var(--st);
  position: relative;
  overflow: hidden;
  color: #e7d7d7;
  font-size: 2rem;
  @include limbo-min {
    &:hover {
      border-radius: 0 15px 0 15px;
    }
  }
}

@mixin btn-1 {
  @include btn;
  background-color: var(--color-3) !important;
  color: var(--color-2) !important;
  border-radius: 0;
}

@mixin btn-2 {
  @include btn;
  background-color: var(--color-4);
  &:not(:first-of-type) {
    @include tab-min {
      margin-left: 2rem;
    }
  }
}

.btn-1 {
  @include btn-1;
}

.btn-2 {
  @include btn-2;
}

@mixin bs {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

@mixin ds {
  filter: drop-shadow(5px 5px 10px rgba(0, 0, 0, 0.1));
}

@mixin br1 {
  border-radius: 0.5rem;
}

@mixin st {
  transition: var(--st);
}

html {
  font-size: 10px;
  @include md-desk-only {
    font-size: 8px;
  }
  @include sm-desk-only {
    font-size: 7px;
  }
  @include limbo-only {
    font-size: 7px;
  }
  @include tab-only {
    font-size: 8px;
  }
  @include mob-only {
    font-size: 7px;
  }
  :where(.wp-block) {
    max-width: 90%;
  }
}

:root {
  @include mob-only {
    --space-1: 6rem;
    --space-2: 5rem;
    --space-3: 4rem;
    --space-4: 3rem;
  }
}
